import Layout from "./Layout";
import {urlOfImages} from './images';

const Personal = () =>
{
    return (<>
        <Layout>
        <div className="block md:flex justify-center p-4">
            <div className="p-4">
                <img src={urlOfImages.user} className=" rounded-full" alt="Profile Picture"/>
            </div>
            <div className="p-4">
            <div class="max-w-sm rounded overflow-hidden shadow-lg">
            <div class="px-6 py-4">
                <div class="font-bold text-4xl mb-2">Username</div>
                <div class="font-bold text-2xl mb-2">email@mail.com</div>
                <p class="text-gray-700 text-base">
                Description about user regarding its various interest and other.
                </p>
                <p>

                </p>
            </div>
           
            </div>
            </div>
        </div>
        </Layout>
        </>
    );

};


export default Personal;
